import React from 'react'
import { Provider } from 'react-redux'
import PropTypes from 'prop-types'
import DatePicker from 'material-ui/DatePicker'
import TextField from 'material-ui/TextField'
import Swipeout from 'rc-swipeout'
import { List, ListItem } from 'material-ui/List'
import Subheader from 'material-ui/Subheader'
import Avatar from 'material-ui/Avatar'
import Divider from 'material-ui/Divider'
import RaisedButton from 'material-ui/RaisedButton'

const isSmallScreen = window.innerWidth <= 800 && window.innerHeight <= 600
const roomNumber = localStorage.getItem('rn') || '668668'

const listStyle = {
  'maxWidth': '400px',
  minHeight: isSmallScreen ? '150%' : '80%',
  margin: 'auto',
  marginTop: '20px',
  border: '1px solid rgb(217, 217, 217)',
  'borderRadius': '5px'
}

const nameFieldStyle = {
  width: '120px',
  marginLeft: '15px'
}

const chipsFieldStyle = {
  width: '60px',
  'marginLeft': '10px'
}

function Subtitle ({ players }) {
  let totalChips = players.reduce((sum, player) => sum += Number(player.chips), 0)
  let totalBalance = players.reduce((sum, player) => sum += Number(player.balance), 0)
  let error = totalBalance === totalChips ? {} : { color: '#F44336' }
  return <Subheader>Players: {players.length},
    Chips: {totalChips},
    Balances: <span style={error}>{totalBalance}</span></Subheader>
}

Subtitle.propTypes = {
  players: PropTypes.array,
}

class ChipForm extends React.Component {
  static propTypes = {
    store: PropTypes.object.isRequired,
  }
  constructor (props) {
    super(props)
    if (localStorage.getItem(roomNumber)) {
      this.state = JSON.parse(localStorage.getItem(roomNumber))
    } else {
      this.state = {
        players: [{ name: '', chips: 200, balance: '' }, { name: '', chips: 200, balance: '' }],
        buyin: 200,
        price: 20,
        startDate: new Date()
      }
    }

    this.startDateChange = this.startDateChange.bind(this)
    this.addPlayer = this.addPlayer.bind(this)
  }

  addPlayer () {
    let players = this.state.players
    players.unshift({
      name: '',
      chips: this.state.buyin,
      balance: ''
    })
    this.setState({
      players
    }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
  }

  removePlayer (index) {
    let self = this

    return (event, name) => {
      let players = self.state.players.filter((_, i) => i !== index)
      self.setState({
        players
      }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
    }
  }

  nameChange (index) {
    let self = this

    return (event, name) => {
      let players = self.state.players
      for (let i = 0; i < players.length; i++) {
        if (i === index) {
          players[i].name = name
        }
      }
      self.setState({
        players
      }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
    }
  }

  chipChange (index) {
    let self = this

    return (event, chips) => {
      let players = self.state.players
      for (let i = 0; i < players.length; i++) {
        if (i === index) {
          players[i].chips = chips === '' ? '' : Number(chips) || 0
        }
      }
      self.setState({
        players
      }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
    }
  }

  balanceChange (index) {
    let self = this

    return (event, balance) => {
      let players = self.state.players
      for (let i = 0; i < players.length; i++) {
        if (i === index) {
          players[i].balance = balance === '' ? '' : Number(balance) || 0
        }
      }
      self.setState({
        players
      }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
    }
  }

  buyInChange = (event, buyin) => {
    this.setState({
      buyin
    }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
  }

  priceChange = (event, price) => {
    this.setState({
      price
    }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
  }

  startDateChange (event, startDate) {
    this.setState({
      startDate
    }, () => localStorage.setItem(roomNumber, JSON.stringify(this.state)))
  }

  inputFocus (event) {
    if (isSmallScreen) {
      event.currentTarget.scrollIntoView()
    }
  }
  render () {
    return (
      <Provider store={this.props.store}>
        <List style={listStyle}>
          <Subheader>Basic</Subheader>
          <ListItem>
            <DatePicker
              textFieldStyle={{ width: '100%' }}
              style={{ width: '30%', marginRight: '10px', display: 'inline-block' }}
              floatingLabelText='Date'
              onChange={this.startDateChange}
              value={new Date(this.state.startDate)}
              hintText='Date' />
            <TextField
              style={{ width: '30%', marginRight: '10px' }}
              floatingLabelText='Buyin Chips'
              onChange={this.buyInChange}
              value={this.state.buyin}
              hintText='Chips/Buyin' />
            $<TextField
              style={{ width: '30%' }}
              floatingLabelText='Buyin Price'
              onChange={this.priceChange}
              value={this.state.price}
              hintText='Price/Buyin' />
          </ListItem>
          <ListItem>
            <RaisedButton
              style={{ marginRight: '10px' }}
              primary
              onTouchTap={this.addPlayer}
              label={`Add Player (${this.state.players.length})`}
            />
          </ListItem>
          <Divider />

          <Subtitle players={this.state.players} />

          {
            this.state.players.map((player, i) => {
              return (
                <ListItem onKeyboardFocus={this.inputFocus}>
                  <Swipeout autoClose
                    right={[
                      {
                        text: 'cancel',
                        style: { backgroundColor: 'rgb(232, 232, 232)', color: 'white', cursor: 'pointer' },
                      }, {
                        text: 'delete',
                        onPress: this.removePlayer(i).bind(this),
                        style: { backgroundColor: 'rgb(255, 64, 129)', color: 'white', cursor: 'pointer' },
                      }
                    ]}
                  >
                    <Avatar>{player.name.charAt(0) || '/'}</Avatar>
                    <TextField
                      style={nameFieldStyle}
                      floatingLabelText='Name'
                      onChange={this.nameChange(i).bind(this)}
                      onFocus={this.inputFocus}
                      ref={(input) => { this.input = input }}
                      value={player.name} />
                    <TextField
                      style={chipsFieldStyle}
                      floatingLabelText='Chips'
                      onChange={this.chipChange(i).bind(this)}
                      onFocus={this.inputFocus}
                      value={player.chips}
                      hintText='Chips' />
                    <TextField
                      style={chipsFieldStyle}
                      floatingLabelText='Balance'
                      onChange={this.balanceChange(i).bind(this)}
                      onFocus={this.inputFocus}
                      value={player.balance}
                      hintText='Balance' />
                  </Swipeout>
                </ListItem>
              )
            })
          }
        </List>
      </Provider>
    )
  }
}

export default ChipForm



