import React from 'react'
import Drawer from 'material-ui/Drawer'
import AppBar from 'material-ui/AppBar'
import MenuItem from 'material-ui/MenuItem'
import RaisedButton from 'material-ui/RaisedButton'
import ActinoMoneyOff from 'material-ui/svg-icons/editor/monetization-on'
import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'
import { List, ListItem } from 'material-ui/List'
import Subheader from 'material-ui/Subheader'
import Avatar from 'material-ui/Avatar'
import moment from 'moment'

const roomNumber = localStorage.getItem('rn') || '668668'

const dialogBodyStyle = {
  backgroundImage: 'url(/podium.png)',
  backgroundRepeat: 'no-repeat',
  backgroundSize: '30%',
  backgroundPositionX: 'center',
  backgroundPositionY: 'bottom',
}

export default class Header extends React.Component {
  constructor (props) {
    super(props)
    this.state = { open: false, openDialog: false }
  }
  handleToggle = () => this.setState({ open: !this.state.open })

  handleClose () { this.setState({ open: false }) }

  handleOpenDialog = () => this.setState({ openDialog: true })
  handleCloseDialog = () => this.setState({ openDialog: false })

  render () {
    const actions = [
      <FlatButton
        label='Close'
        primary
        onTouchTap={this.handleCloseDialog}
      />,
    ]
    return (
      <div>
        <Drawer
          docked={false}
          open={this.state.open}
          onRequestChange={
            (open) => this.setState({ open })
          }
          >
          <MenuItem onTouchTap={this.handleClose.bind(this)}>Menu Item 1</MenuItem>
          <MenuItem onTouchTap={this.handleClose.bind(this)}>Menu Item 2</MenuItem>
          <MenuItem onTouchTap={this.handleClose.bind(this)}>Menu Item 3</MenuItem>
        </Drawer>
        <Dialog
          contentStyle={{ transform: 'none', maxHeight: '90%' }}
          bodyStyle={dialogBodyStyle}
          modal={false}
          open={this.state.openDialog}
          onRequestClose={this.handleCloseDialog}
          autoScrollBodyContent
        >
          {moment(JSON.parse(localStorage.getItem(roomNumber)) || { startDate: new Date() }.startDate).format('MM/DD/YYYY')}
          <List>
            {
              (JSON.parse(localStorage.getItem(roomNumber)) || { players: [] }).players.sort((a, b) => {
                return (Number(b.balance) - Number(b.chips)) - (Number(a.balance) - Number(a.chips))
              }).map((player, i) => {
                let avatar = i < 3
                  ? <Avatar src={`/medal-${i + 1}.png`} backgroundColor={'#fff'} size={20} />
                  : <Avatar size={20}>{ i + 1 }</Avatar>
                let revenue = player.balance - player.chips
                let revenueColor = revenue > 0 ? '#B71C1C' : revenue < 0 ? '#388E3C' : '#BDBDBD'
                return (
                  <ListItem
                    innerDivStyle={{ paddingTop: '10px', paddingBottom: '10px' }}
                    leftAvatar={avatar}
                  >
                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                      <div style={{ minWidth: '40%' }}>{player.name}</div>
                      <div style={{ color: '#BDBDBD' }}>{player.chips}</div>
                      <div style={{ color: revenueColor, minWidth: '10%', textAlign: 'right' }}>
                        {revenue > 0 ? '+' : ''}{revenue}
                      </div>
                    </div>
                  </ListItem>
                )
              })
            }
          </List>
        </Dialog>
        <AppBar title='德扑庄主'
          iconElementRight={<RaisedButton
            label='Calculate'
            labelPosition='before'
            secondary
            icon={<ActinoMoneyOff />}
          />}
          onRightIconButtonTouchTap={this.handleOpenDialog}
          onLeftIconButtonTouchTap={this.handleToggle} />
      </div>
    )
  }
}



